* {
  margin: 0;
}

body {
  background-color: gainsboro;
}

.app__header {

  display: flex;
  align-items: center;
  margin: 20px;
  justify-content: space-between;
}

.app__dropdown{
  background-color: white;
}
.app__header > h1{
  color: #fc3c3c;
  font-size:2rem;
}

.app__stats {
  display: flex;
  justify-content: space-between;
}

.app__left{
  flex:0.9;
}

.app__right{
  display:flex;
  flex-direction: column;

}

.app__right .MuiCardContent-root{
  display: flex;
  flex-direction:column;
  flex-grow: 1;
}

.app {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

@media(max-width:990px){
  .app{
    flex-direction: column;
  }
}

.app__graph{
  flex-grow: 1;
}

.app_graphTitle{
  margin-top: 20px;
  margin-bottom: 20px;
}